import { useSelector, useDispatch } from 'react-redux';

import { counterActions } from '../redux/reducers';
import classes from './Counter.module.css';

const Counter = () => {
  const dispatch = useDispatch();

  const counter = useSelector((state) => state.counterState.counter);
  const showCounter = useSelector((state) => state.counterState.showCounter);

  const incrementHandler = () => dispatch(counterActions.increment());
  
  const increaseHandler = () => dispatch(counterActions.increase(5))

  const decrementHandler = () => dispatch(counterActions.decrement());

  const clearHandler = () => dispatch(counterActions.clear());

  const toggleCounterHandler = () => dispatch(counterActions.toggleCounter());

  return (
    <main className={classes.counter}>
      <h1>Redux Counter</h1>
      { showCounter && <div className={classes.value}>{ counter }</div> }
      <div>
        <button onClick={incrementHandler}>Increment</button>
        <button onClick={increaseHandler}>Increase by 5</button>
        <button onClick={decrementHandler}>Decrement</button>
        <button onClick={clearHandler}>Clear</button>
      </div>
      <button onClick={toggleCounterHandler}>Toggle Counter</button>
    </main>
  );
};

export default Counter;
